import { Component, Prop, h, Element } from '@stencil/core';
import { utils } from '../../utils/utils';

@Component({
  tag: 'yeti-page-contents',
  shadow: false,
})
export class YetiPageContents {

  @Element() el: HTMLElement;

  /**
   * Whether to show the Page Contents menu in expanded or closed state.
   */
  @Prop() isExpanded: boolean = false;


  /**
   * Whether to show the Page Contents menu in expanded or closed state.
   */
  @Prop() wrapperId: string = ""; // Will be set in componentWillLoad;


  componentWillLoad() {
    
    let elId = this.el.id;
    let elsParent;

    // Hide this whole component from screen-readers (since they have a better version built in)
    this.el.setAttribute("aria-hidden", "true");

    // Set up ids
    if (!elId) {
      elId = utils.generateUniqueId();
      this.wrapperId = `${elId}_wrapper`;
    }

    // Find el's wrapping div, main, or article block.
    elsParent = this.el.closest(':is("div", "main", "article")');
    if (elsParent) {
      console.log("El's parent is:", elsParent);
    } else {
      console.log("El's an orphan. :(");
    }

  }



  render() {

    return (
      
      <div class="yeti-page_contents-wrapper" id={this.wrapperId}>Page Contents Under Construction</div>

    );
  }
  

}
